/* http://meyerweb.com/eric/tools/css/reset/
   v5.0.1 | 20191019
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, menu, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block;
}

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none;
}

body {
  line-height: 1;
}

menu, ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
}

* {
  box-sizing: border-box;
}

.srOnly {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
}

@keyframes fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes expand {
  from {
    flex-grow: 0;
    transform: scale(0.7);
  }
  to {
    flex-grow: 1;
    transform: scale(1);
  }
}
@keyframes grow {
  from {
    width: 0;
    height: 0;
  }
  to {
    width: 250%;
    height: 250%;
  }
}
@keyframes flip {
  0% {
    transform: translate(-50%, -50%) rotateX(-2160deg);
    opacity: 0;
  }
  100% {
    transform: translate(-50%, -50%) rotateX(0);
    opacity: 1;
  }
}
@keyframes flip-scale {
  0% {
    transform: scale(1) rotateX(0);
  }
  50% {
    transform: scale(2) rotateX(-90deg);
  }
  100% {
    transform: scale(1) rotateX(0);
  }
}
.expand {
  opacity: 0;
  animation: expand 0.5s, fade 0.25s 0.25s;
}

.flip {
  animation: flip 1.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}

.flip-scale {
  animation: flip-scale 0.6s linear both;
}

.game {
  display: grid;
  grid-template-rows: auto minmax(3.2rem, 2fr) auto minmax(3.2rem, 1fr) auto;
  justify-items: center;
  min-height: 100vh;
  padding: 3.2rem 3.2rem 5.5rem;
  overflow: hidden;
  background: radial-gradient(circle at top, #1f3756, #141539);
}
@media (min-width: 40.625rem) {
  .game {
    grid-template-rows: auto 5.86vh auto 1fr auto;
    padding: 4.6rem 3.2rem 3.2rem;
  }
}
.game .header {
  width: calc(100% + 0.2rem);
  max-width: 70.2rem;
  margin: -0.1rem;
}
.game .content {
  grid-row: 3;
}
@media (min-width: 40.625rem) {
  .game .content {
    width: 100%;
    margin-bottom: -1.1rem;
  }
}
.game .footer {
  grid-row: 5;
}
@media (min-width: 40.625rem) {
  .game .footer {
    justify-self: end;
  }
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 1rem 1rem 2.1rem;
  border: 0.3rem solid #606e85;
  border-radius: 0.6rem;
}
@media (min-width: 40.625rem) {
  .header {
    padding: 2.1rem 2.2rem 1.5rem 2.8rem;
    border-radius: 1.6rem;
  }
}

.title {
  box-sizing: content-box;
  width: 5rem;
  height: auto;
}
@media (min-width: 40.625rem) {
  .title {
    width: 11.5rem;
  }
}

.score {
  display: flex;
  flex-flow: column;
  align-items: center;
  align-self: flex-start;
  min-width: 8rem;
  padding: 1rem;
  background: white;
  border-radius: 0.4rem;
}
@media (min-width: 40.625rem) {
  .score {
    min-width: 15rem;
    margin: -0.2rem 0;
    padding: 1.5rem;
    border-radius: 0.8rem;
  }
}
.score > div:nth-child(1) {
  color: #2a46c0;
  font: 600 1rem Barlow Semi Condensed, sans-serif;
  letter-spacing: 0.16em;
  text-transform: uppercase;
}
@media (min-width: 40.625rem) {
  .score > div:nth-child(1) {
    font-size: 1.7rem;
    letter-spacing: 0.12em;
  }
}
.score > div:nth-child(2) {
  color: #3b4363;
  font: 700 4rem/100% Barlow Semi Condensed, sans-serif;
  letter-spacing: -0.02em;
}
@media (min-width: 40.625rem) {
  .score > div:nth-child(2) {
    font-size: 6.4rem;
    letter-spacing: normal;
  }
}

.box {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 100%;
}
.box > * {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  transform: translate(-50%, -50%);
}

button {
  display: inline-flex;
  text-align: center;
  text-transform: uppercase;
  background: transparent;
  border: none;
  cursor: pointer;
}

.playField {
  width: 100%;
  max-width: 40rem;
  margin-bottom: 10.67vh;
}
@media (min-width: 40.625rem) {
  .playField {
    max-width: 47.2rem;
    margin-bottom: 0;
  }
}
.playField.fadeIn {
  animation: fade 0.5s;
}
.playField.fadeOut {
  animation: fade 0.5s reverse;
}
.playField > .box {
  padding-bottom: 97.46%;
}
.playField > .box > .grid {
  display: grid;
  grid-template: "  .     .      .    scissors scissors scissors  .     .     .  " 129fr "spock spock    .    scissors scissors scissors  .   paper paper" 16fr "spock spock    .       .        .        .      .   paper paper" 129fr "  .     .      .       .        .        .      .     .     .  " 41fr "  .   lizard lizard  lizard     .      rock    rock rock    .  " 145fr/65fr 80fr 19fr 46fr 52fr 47fr 18fr 80fr 65fr;
  background: center/70% no-repeat url(bg-pentagon.svg);
}

.option {
  --gradient-start: hsl(0, 0%, 45%);
  --gradient-end: hsl(0, 0%, 55%);
  --color-outer-shadow: #5a5a5a;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  font-size: 1rem;
  background: linear-gradient(to top, var(--gradient-start), var(--gradient-end));
  border-radius: 50%;
  box-shadow: inset 0 -0.2em var(--color-outer-shadow), 0 0.2em var(--color-outer-shadow);
  transition: filter 0.15s, transform 0.2s;
}
@media (min-width: 40.625rem) {
  .option {
    font-size: 1.5rem;
  }
}
.option > span {
  display: block;
  width: 77%;
  height: 77%;
  background: #e6e6e6;
  border-radius: 50%;
  box-shadow: inset 0 0.4em rgba(59, 67, 99, 0.2);
}

button.option:focus, button.option:hover {
  outline: none;
  filter: drop-shadow(0 0 0.6rem var(--gradient-start));
}
button.option:active {
  transform: scale(0.95);
}

.scissors {
  --gradient-start: #ec9e0e;
  --gradient-end: #eca922;
  --color-outer-shadow: #bc7f06;
  grid-area: scissors;
}

.spock {
  --gradient-start: #40b9ce;
  --gradient-end: #52bed1;
  --color-outer-shadow: #229eb4;
  grid-area: spock;
}

.paper {
  --gradient-start: #4865f4;
  --gradient-end: #5671f5;
  --color-outer-shadow: #012bfe;
  grid-area: paper;
}

.lizard {
  --gradient-start: #834fe3;
  --gradient-end: #8c5de5;
  --color-outer-shadow: #5c16df;
  grid-area: lizard;
}

.rock {
  --gradient-start: #dc2e4e;
  --gradient-end: #dd405d;
  --color-outer-shadow: #bc1534;
  grid-area: rock;
}

.scissors > span {
  padding: 20% 22.8% 16.6% 18.6%;
}

.spock > span {
  padding: 18.6% 20.7% 17.2% 24.8%;
}

.paper > span {
  padding: 20% 22.1% 15.9% 20.7%;
}

.lizard > span {
  padding: 19.3% 16.6% 15.9% 16.6%;
}

.rock > span {
  padding: 24.1% 21.4% 19.3% 22.1%;
}

.rules {
  padding: 0.9rem 3.6rem;
  color: white;
  font: 600 1.7rem Barlow Semi Condensed, sans-serif;
  letter-spacing: 0.12em;
  border: 0.2rem solid white;
  border-radius: 0.8rem;
  transition: box-shadow 0.15s, background-color 0.25s;
}
.rules:focus {
  border-color: #2a46c0;
  outline: none;
}
.rules:hover, .rules:active {
  background-color: rgba(255, 255, 255, 0.1);
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  display: flex;
  width: 100%;
  height: 100%;
  overflow: auto;
  background: rgba(0, 0, 0, 0.5);
  transition: opacity 1s;
}
.overlay.fadeIn {
  animation: fade 0.3s;
}
.overlay.fadeOut {
  animation: fade 0.3s reverse;
}

.modal {
  z-index: 105;
  display: grid;
  grid-template-rows: auto 95fr auto 138fr auto;
  justify-items: center;
  width: 100%;
  height: 100%;
  margin: auto;
  padding: 9.5rem 3.2rem 6.5rem;
  background: white;
  border-radius: 0.8rem;
}
@media (min-width: 28.125rem) {
  .modal {
    grid-gap: 2rem;
    grid-template: repeat(2, auto)/repeat(2, auto);
    max-width: 40rem;
    height: auto;
    padding: 3.2rem 3.2rem 4.7rem;
  }
}
.modal > h2 {
  color: #3b4363;
  font: 700 3.2rem/100% Barlow Semi Condensed, sans-serif;
  text-transform: uppercase;
}
@media (min-width: 28.125rem) {
  .modal > h2 {
    align-self: center;
    justify-self: start;
  }
}
.modal > img {
  grid-row: 3;
  width: 100%;
  height: auto;
}
@media (min-width: 28.125rem) {
  .modal > img {
    grid-area: 2/1/-1/-1;
  }
}
.modal > button {
  grid-row: 5;
  margin: -1.4rem;
  padding: 1.4rem;
  border-radius: 50%;
  transition: background-color 0.25s, box-shadow 0.25s;
}
@media (min-width: 28.125rem) {
  .modal > button {
    grid-area: 1/2;
    align-self: center;
    justify-self: end;
  }
}
.modal > button:focus {
  outline: none;
  box-shadow: inset 0 0 0 0.2rem #b8baca;
}
.modal > button:hover {
  background-color: #e1e4ed;
}
.modal > button:active {
  background-color: #b8baca;
}

.result {
  display: flex;
  flex-flow: wrap;
  justify-content: center;
  max-width: 40rem;
  margin: 0 -0.3rem;
}
@media (min-width: 58.125rem) and (orientation: landscape) {
  .result {
    flex-flow: nowrap;
    max-width: 67.2rem;
    margin: 2.6rem 0 0;
  }
}
.result.fadeIn {
  animation: fade 0.6s ease-in;
}
.result.fadeOut {
  animation: fade 0.6s ease-in reverse;
}
.result > .col {
  --mx: 2.85rem;
  display: flex;
  flex: 1 1 calc(50% - var(--mx));
  flex-flow: column;
  align-items: center;
  min-width: 0;
}
@media (min-width: 58.125rem) and (orientation: landscape) {
  .result > .col {
    --mx: 4.3rem;
    flex: 0 1 29.3rem;
  }
}
.result > .col:nth-child(1) {
  margin-right: var(--mx);
}
.result > .col:nth-child(2) {
  margin-left: var(--mx);
}
@media (min-width: 58.125rem) and (orientation: landscape) {
  .result > .col:nth-child(2) {
    order: 1;
  }
}
.result > .col > .label {
  margin-top: 2.6rem;
}
@media (min-width: 58.125rem) and (orientation: landscape) {
  .result > .col > .label {
    order: -1;
    margin-top: 0;
    margin-bottom: 6.4rem;
  }
}
.result > .col > .box::before {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0%;
  height: 0%;
  background: center/contain no-repeat radial-gradient(circle at center, rgba(255, 255, 255, 0.09) 0%, rgba(255, 255, 255, 0.09) 40%, rgba(255, 255, 255, 0.06) 40%, rgba(255, 255, 255, 0.06) 54%, rgba(255, 255, 255, 0.03) 54%, rgba(255, 255, 255, 0.03) 100%);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  transition: width 1s, height 1s;
  content: "";
}
.result > .col > .box.highlight::before {
  width: 220%;
  height: 220%;
}
@media (min-width: 58.125rem) and (orientation: landscape) {
  .result > .col > .box.highlight::before {
    width: 250%;
    height: 250%;
  }
}
.result > .center {
  flex: 1;
  align-self: center;
  margin-top: 9vh;
  visibility: hidden;
}
@media (min-width: 58.125rem) and (orientation: landscape) {
  .result > .center {
    flex: 0 1 0;
    margin-top: 6rem;
    overflow: hidden;
  }
}
.result > .center > :nth-child(2) {
  margin-top: 1.6rem;
}
.result > .center > .status {
  text-align: center;
}
.result > .center > .repeat {
  max-width: 22rem;
  margin-right: auto;
  margin-left: auto;
}
.result.expanded > .center {
  flex-grow: 1;
  visibility: visible;
}
@media (min-width: 58.125rem) and (orientation: landscape) {
  .result.expanded {
    max-width: 96rem;
  }
}
.result .option {
  font-size: 1.5rem;
}
@media (min-width: 58.125rem) and (orientation: landscape) {
  .result .option {
    font-size: 3rem;
  }
}

.square {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 100%;
}
.square > * {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.placeholder {
  width: 77%;
  height: 77%;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 50%;
}

.label {
  color: white;
  font: 700 1.6rem Barlow Semi Condensed, sans-serif;
  letter-spacing: 0.09em;
  white-space: nowrap;
  text-transform: uppercase;
}
@media (min-width: 58.125rem) and (orientation: landscape) {
  .label {
    font-size: 2.4rem;
    letter-spacing: 0.12em;
  }
}

.status {
  color: white;
  font: 700 5.6rem Barlow Semi Condensed, sans-serif;
  white-space: nowrap;
  text-transform: uppercase;
}

.repeat {
  display: block;
  width: 100%;
  padding: 1.4rem;
  color: #3b4363;
  font: 600 1.7rem Barlow Semi Condensed, sans-serif;
  letter-spacing: 0.12em;
  background: white;
  border-radius: 0.8rem;
  transition: box-shadow 0.15s, background-color 0.25s;
}
.repeat:focus {
  outline: none;
  box-shadow: 0 0 0 0.3rem #2a46c0;
}
.repeat:hover, .repeat:active {
  background-color: rgba(255, 255, 255, 0.75);
}

.hidden {
  display: none;
}

.header,
.footer,
.result > *,
.col > * {
  z-index: 5;
}